@import "../../util/scss/mixins";
.aboutcity {
  display: flex;
  justify-content: space-between;
  & &__title {
    text-align: left;
  }
  &__text {
    font-size: 1.8rem;
  }
  &__leftside {
    width: 60%;
  }
  &__rightside {
    width: 35%;
  }
  &__map {
    margin-top: 3rem;
    width: 60%;
    height: 45rem;
  }
}
.cityimages {
  &--top,
  &--bottom {
    height: 50%;
  }
  &__primaryImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-bottom: 2px solid var(--dark-gray);
  }
  &__secondaryImg {
    width: 50%;
    height: 50%;
    object-fit: cover;

    &:last-child {
      display: inline-block;
      position: relative;
      & > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &--seeMore {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.6);
      width: 100%;
      height: 100%;
      & > h3 {
        color: white;
        @include centerElement;
        font-size: 2.5rem;
      }
    }
  }
}
